<template>
	<input
		:id="field_id"
		:name="field_id"
		v-model="value"
		@focus="open_calendar"
		@blur="close_calendar"
		@keyup.enter.prevent="close_calendar"
		type="text"
	>

	<div v-show="is_opened" class="vdp-datepicker" ref="calendar_reference" >
		<div class="calendar">
			<header>
				<span class="prev" @click="prev">&lt;</span>
				<span class="go_up" @click="go_up">
					{{ up_the_text }}
				</span>
				<span class="next" @click="next">&gt;</span>
			</header>

			<!-- days -->
			<div
				v-if="calendar_type === 0"
			>
				<span
					v-for="weekday in construct_week"
					class="cell day-header"
				>
					{{ weekday }}
				</span>
				<span
					v-for="day in construct_days"
					@click="day_picked(day)"
					class="cell day"
					:class="[day === this_day ? 'today' : '', day === 0 ? 'blank' : '', day === picked_day ? 'picked' : '']"
				>
					{{ day === 0 ? '' : day }}
				</span>
			</div>

			<!-- months -->
			<div
				v-if="calendar_type === 1"
			>
				<span
					v-for="(number, month) in construct_months"
					@click="month_picked(number)"
					class="cell month"
					:class="number === this_month ? 'today' : ''"
				>
					{{ month }}
				</span>
			</div>

			<!-- years -->
			<div
				v-if="calendar_type === 2"
			>
				<span
					v-for="year in construct_years"
					@click="year_picked(year)"
					class="cell year"
					:class="year === this_year ? 'today' : ''"
				>
					{{ year }}
				</span>
			</div>

		</div>
	</div>

</template>












<script>
	import Date_helpers from '@/components/forms/date_library/Date_helpers.js';

	// displayed type: day, month, year
	// listener na sipky: zmena hore/dole podla typu
	// listener medzi sipkami: zmena na mesia alebo rok
	// listener na vyber: den, mesiac rok
	// vypis datumu do inputu
	// prijatie datumu zo session
	// prijatie datumu z inputu: user text input change
	// osetrenie chybnych formatov
	// zobrazenie podla dodaneho datumu, a dnesneho datumu
	// zobrazenie dnesneho datumu ako default ak doruceny prazdny string
	// zobrazenie vo vybranom type: den, mesiac, rok
	// zobrazenie kalendaru pre aktualny mesiac a spravne zoradenie dni
	// zobrazenie aktualneho roku
	// zobrazenie aktualnej dekady
	// disabled dates
	// reactivity events
	// store eventy
	// emity, product form, global form
	// otvori sa aj nad inputom

	export default {
		props: {
			field_id: String,
			field_data: Object,
			init_type: String, // days, months, years, eighties
		},
		components: {
		},
		emits: [],
		mixins: [],
		data() {
			return {
				is_opened: false,
				calendar_type: undefined,
				value: '',
				shown: {
					day: 0,
					month: 0,
					year: 0,
					decade: 0,
				},
				picked: {
					day: 0,
					month: 0,
					year: 0,
					decade: 0,
				},
			}
		},
		created() {
			let type_definition = {
				days: 0,
				months: 1,
				years: 2,
				eighties: 3,
			};
			this.calendar_type = type_definition[this.init_type];
		},
		mounted() {
		},
		methods: {
			prev()
			{
				if (this.calendar_type === 2)
				{
					this.shown.decade -= 30;
				}
				else if (this.calendar_type === 1)
				{
					this.shown.year--;
				}
				else if (this.calendar_type === 0)
				{
					if (this.shown.month === 1)
					{
						this.shown.month = 12;
						this.shown.year--;
					}
					else
					{
						this.shown.month--;
					}
				}
			},
			next()
			{
				if (this.calendar_type === 2)
				{
					this.shown.decade += 30;
				}
				else if (this.calendar_type === 1)
				{
					this.shown.year++;
				}
				else if (this.calendar_type === 0)
				{
					if (this.shown.month === 12)
					{
						this.shown.month = 1;
						this.shown.year++;
					}
					else
					{
						this.shown.month++;
					}
				}
			},
			go_up()
			{
				if (this.calendar_type === 2) {
					return;
				}
				this.calendar_type++;
			},
			day_picked(int)
			{
				this.shown.day = int;
				this.picked = this.shown;
				this.update_input();
				this.is_opened = false;
			},
			month_picked(int)
			{
				this.shown.month = int;
				this.calendar_type--;
			},
			year_picked(int)
			{
				this.shown.year = int;
				this.calendar_type--;
			},
			update_input()
			{
				this.value = this.picked.day.toString().padStart(2, '0') + '.' + this.picked.month.toString().padStart(2, '0') + '.' + this.picked.year;
			},
			open_calendar()
			{
				this.shown = Date_helpers.default_shown(this.value);

				this.is_opened = true;
			},
			close_calendar(event)
			{
				// dont close calendar if focus was lost inside calendar
				if (this.$refs.calendar_reference.contains(event.explicitOriginalTarget)) {
					return;
				}
				this.is_opened = false;
				this.picked = Date_helpers.default_shown(this.value);
				this.update_input();
				this.calendar_type = 0;
			},
			// user_changed_value()
			// {
			// 	this.shown = Date_helpers.default_shown(this.value);
			// 	this.picked = this.shown;
			// 	this.calendar_type = 0;
			// },
		},
		computed: {
			construct_days()
			{
				return Date_helpers.construct_days(this.shown.year, this.shown.month);
			},
			construct_week()
			{
				return Date_helpers.construct_week();
			},
			construct_months()
			{
				return Date_helpers.construct_months();
			},
			construct_years()
			{
				return Date_helpers.construct_years(this.shown.decade);
			},
			this_day()
			{
				let now = new Date();
				if (
					now.getMonth() + 1 === this.shown.month
					&&
					now.getFullYear() === this.shown.year
				) {
					return now.getDate();
				}
				return null;
			},
			picked_day()
			{
				if (this.value === '') {
					return null;
				}
				if (
					this.picked.month === this.shown.month
					&&
					this.picked.year === this.shown.year
				) {
					return this.picked.day;
				}
				return null;
			},
			this_month()
			{
				let now = new Date();
				if (
					now.getFullYear() === this.shown.year
				) {
					return now.getMonth() + 1;
				}
				return false;
			},
			this_year()
			{
				return new Date().getFullYear();
			},
			up_the_text()
			{
				if (this.calendar_type === 0)
				{
					return Date_helpers.show_month_name(this.shown.month) + ' ' + this.shown.year;
				}
				else if (this.calendar_type === 1)
				{
					return this.shown.year;
				}
				else if (this.calendar_type === 2)
				{
					return this.shown.decade + ' - ' + (this.shown.decade + 29);
				}
				else
				{
					return 'should not occur';
				}
			},
			// value:
			// {
			// 	get() {
			// 		return '';
			// 	},
			// 	set(event_value) {
			// 		console.log(event_value);
			// 	},
			// },
		},
		watch: {
		},
	}
</script>





































<style lang="scss">

	.vdp-datepicker {
		position: relative;
		text-align: left;
		* {
			box-sizing: border-box;
		}

		.calendar {
			position: absolute;
			z-index: 100;
			background: #fff;
			width: 300px;
			border: 1px solid #ccc;
			header {
				display: block;
				line-height: 40px;
				span {
					display: inline-block;
					text-align: center;
					width: 58%;
					float: left;
				}
				.prev {
					&:after {
						border-right: 10px solid #000;
						margin-left: -5px;
					}
					&.disabled {
						&:after {
							border-right: 10px solid #ddd;
						}
					}
				}
				.next {
					&:after {
						border-left: 10px solid #000;
						margin-left: 5px;
					}
					&.disabled {
						&:after {
							border-left: 10px solid #ddd;
						}
					}
				}
			}
			.disabled {
				color: #ddd;
				cursor: default;
			}
			.cell {
				display: inline-block;
				padding: 0 5px;
				width: 14.285714285714286%;
				height: 40px;
				line-height: 40px;
				text-align: center;
				vertical-align: middle;
				border: 1px solid transparent;
				&.selected {
					background: maroon;
					&:hover {
						background: maroon;
					}
					&.highlighted {
						background: maroon;
					}
				}
				&.highlighted {
					background: #cae5ed;
					&.disabled {
						color: #a3a3a3;
					}
				}
				&.grey {
					color: #888;
					&:hover {
						background: inherit;
					}
				}
				&.day-header {
					font-size: 75%;
					white-space: nowrap;
					cursor: inherit;
					&:hover {
						background: inherit;
					}
				}
				&.today {
					background-color: moccasin;
				}
				&.picked {
					background-color: darksalmon;
				}
			}
		}
		.calendar header .prev,
		.calendar header .next {
			width: 21%;
			float: left;
			// text-indent: -10000px;
			position: relative;
		}
		.calendar header .prev:after,
		.calendar header .next:after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translateX(-50%) translateY(-50%);
			border: 6px solid transparent;
		}
		.calendar header .prev:not(.disabled),
		.calendar header .next:not(.disabled),
		.calendar header .go_up:not(.disabled) {
			cursor: pointer;
		}
		.calendar header .prev:not(.disabled):hover,
		.calendar header .next:not(.disabled):hover,
		.calendar header .go_up:not(.disabled):hover {
			background: mistyrose;
		}
		.calendar .cell:not(.blank):not(.disabled).day,
		.calendar .cell:not(.blank):not(.disabled).month,
		.calendar .cell:not(.blank):not(.disabled).year {
			cursor: pointer;
		}
		.calendar .cell:not(.blank):not(.disabled).day:hover,
		.calendar .cell:not(.blank):not(.disabled).month:hover,
		.calendar .cell:not(.blank):not(.disabled).year:hover {
			border: 1px solid maroon;
		}
		.calendar .month {
			width: 33.333%;
		}
		.calendar .year {
			width: 20%;
